"use strict";
// Convert from '.mermaid>svg' to embedded img
var isSVGSVGElement = function (element) {
    return (element instanceof SVGSVGElement);
};
var convertSVGto = function (svgElement) {
    // Create canvas for converting image to data URL
    var image = document.createElement("img");
    var parent = svgElement.parentElement;
    image.style.width = svgElement.width + "px";
    image.style.height = svgElement.height + "px";
    var xml = new XMLSerializer().serializeToString(svgElement);
    var svg64 = btoa(unescape(encodeURIComponent(xml)));
    var image64 = 'data:image/svg+xml;base64,' + svg64;
    // Get data URL encoding of image
    image.setAttribute("src", image64);
    parent.replaceChild(image, svgElement);
};
if (window.mermaid) {
    window.mermaid.mermaidAPI.initialize({
        flowchart: { htmlLabels: false },
        htmlLabels: false,
        mermaid: {
            callback: function (id) {
                var svgElement = document.getElementById(id);
                if (!isSVGSVGElement(svgElement))
                    return;
                console.log('converting svg(' + id + ') to dataUri');
                convertSVGto(svgElement);
            }
        }
    });
}
